/* eslint-disable comma-dangle */
<template>
  <div style="background-color:#F8F8FA">
    <loading :active.sync="isLoading"></loading>
    <div class="c">
      <div class="row justify-content-center align-items-center">
        <div class="col-sm-10 align-self-center p-3 mt-3">
          <validation-observer v-slot="{invalid}">
            <form @submit.prevent="createOrder" class="text-left" style="color:black">
              <div class="row">
                <!-- 購買的商品 -->
                <h6 class="text-left">購物車 ( {{ carts.length }} 件 )</h6>
                <div id="orderInfo" class="col-md-12 align-self-start my-3 p-3">
                  <!-- 項目 -->
                  <section>
                    <div class="col-md-12 d-flex justify-content-between finish_cart_info mb-3 p-2">
                      <span class="col-md-4">商品資料</span>
                      <span class="col-md-3">單件價格</span>
                      <span class="col-md-2">數量</span>
                      <span class="col-md-3">小計</span>
                    </div>
                    <div
                      class="d-flex col-md-12 p-2 my-3 align-items-center"
                      v-for="item in carts"
                      :key="item.product + 1"
                    >
                      <div class="col-md-4 finish_img d-flex align-items-center p-0">
                        <img :src="item.product.imageUrl[0]" class="mr-3" />
                        <span>{{ item.product.title }}</span>
                      </div>
                      <div class="col-md-3 p-0">
                        <div>NT.${{ item.product.price }}</div>
                      </div>
                      <div class="col-md-2 p-0">
                        <div class="ml-2 m-0">{{ item.quantity }}</div>
                      </div>
                      <div class="col-md-3 p-0">NT${{ item.product.price * item.quantity}}</div>
                    </div>
                  </section>
                  <hr>
                  <!-- 總結欄 -->
                  <section>
                    <div class="table d-flex justify-content-end d-flex">
                      <tr>
                        <th>運費</th>
                        <th v-if="cartTotal>=1000">NT.{{ 0 }}</th>
                        <th v-else>NT.{{ 80 }}</th>
                      </tr>
                      <tr>
                        <th>小計</th>
                        <th>NT.{{ cartTotal }}</th>
                      </tr>
                    </div>
                  </section>
                </div>
                <!-- 填寫訂購資訊 -->
                <div id="purchaseInfo" class="col-md-8 align-self-start my-3">
                  <h6 class="text-left">訂購資訊</h6>
                  <hr />
                  <!-- 訂購人姓名 -->
                  <div class="form-group">
                    <validation-provider v-slot="{errors, classes}" rules="required">
                      <label for="username">訂購人姓名</label>
                      <input
                        type="text"
                        id="username"
                        class="form-control"
                        v-model="form.name"
                        :class="classes"
                      />
                      <span v-if="errors[0]" class="text-danger">{{errors[0]}}</span>
                    </validation-provider>
                  </div>
                  <!-- 訂購人信箱 -->
                  <div class="form-group">
                    <validation-provider v-slot="{errors, classes}" rules="required|email">
                      <label for="email">訂購人信箱</label>
                      <input
                        type="email"
                        id="email"
                        class="form-control"
                        v-model="form.email"
                        :class="classes"
                      />
                      <span v-if="errors[0]" class="text-danger">{{errors[0]}}</span>
                    </validation-provider>
                  </div>
                  <!-- 訂購人電話 -->
                  <div class="form-group">
                    <validation-provider v-slot="{ errors, classes }" rules="required|min:8|max:10">
                      <label for="phone">訂購人電話</label>
                      <input
                        id="phone"
                        v-model="form.tel"
                        type="tel"
                        class="form-control"
                        :class="classes"
                      />
                      <span v-if="errors[0]" class="text-danger">{{ errors[0] }}</span>
                    </validation-provider>
                  </div>
                  <!-- 訂購地址 -->
                  <div class="form-group">
                    <validation-provider v-slot="{ errors, classes }" rules="required">
                      <label for="address">地址</label>
                      <input
                        id="address"
                        v-model="form.address"
                        type="text"
                        class="form-control"
                        :class="classes"
                      />
                      <span v-if="errors[0]" class="text-danger">{{ errors[0] }}</span>
                    </validation-provider>
                  </div>
                  <!-- 付款方式 -->
                  <div class="form-group">
                    <label for="payment">付款方式</label>
                    <select id="payment" v-model="form.payment" class="form-control" required>
                      <option value selected disabled>請選擇付款方式</option>
                      <option value="WebATM">WebATM</option>
                      <option value="ATM">ATM轉帳付款</option>
                      <option value="CVS">超商取貨付款</option>
                      <option value="Barcode">Barcode</option>
                      <option value="Credit">信用卡</option>
                      <option value="ApplePay">ApplePay</option>
                      <option value="GooglePay">GooglePay</option>
                    </select>
                  </div>
                  <!-- 備註 -->
                  <div class="form-group">
                    <label for="message">備註</label>
                    <textarea
                      id="message"
                      v-model="form.message"
                      class="form-control"
                      cols="30"
                      rows="3"
                    ></textarea>
                  </div>
                </div>
                <!-- 訂單資訊 -->
                <div class="col-md-4">
                  <div class="card">
                    <div class="card-header">訂單資訊</div>
                    <div class="card-body">
                      <div class="d-flex justify-content-between">
                      <span>小計</span>
                      <span>NT.{{ cartTotal }}</span>
                    </div>
                    <div class="d-flex justify-content-between  my-3">
                      <span>運費</span>
                      <span v-if="cartTotal>=1000">NT.{{ 0 }}</span>
                      <span v-else>NT.{{ 80 }}</span>
                    </div>
                      <label for="">優惠代碼</label>
                    <div class="form-group d-flex order_coupon">
                      <input type="text" id="order_coupon" class="form-control ">
                      <a class="btn  btn-primary text-nowrap text-nowrap ml-2" href="">
                        套用
                      </a>
                    </div>
                    <hr>
                    </div>
                  </div>
                </div>
                <!-- order -->
              </div>
              <div class="fnbar row d-flex justify-content-between align-items-center mt-3">
                <div class="col-xs-4 d-flex justify-content-start align-items-center pl-3">
                  <router-link to="/cart" class="btn btn-outline-primary">回購物車</router-link>
                </div>
                <div class="col-xs-4 d-flex justify-content-end align-items-center pr-3">
                  <button type="submit" class="btn btn-primary" :disabled="invalid">確認訂購</button>
                </div>
              </div>
            </form>
            <!-- 購物車方向 -->
          </validation-observer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Order',
  data () {
    return {
      isLoading: false,
      fullPage: true,
      form: {
        email: '',
        name: '',
        tel: '',
        address: '',
        payment: 'WebATM',
        message: ''
      },
      coupon: {},
      coupon_code: '',
      carts: {},
      cartTotal: 0,
      quantityTatol: 0,
      orignalTotal: 0,
      uuid: process.env.VUE_APP_UUID
    }
  },
  created () {
    this.getCart()
  },
  methods: {
    getCart () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/shopping`
      this.$http
        .get(url)
        .then((res) => {
          this.carts = res.data.data
          this.calTotal()
          this.isLoading = false
        })
        .catch((err) => {
          this.isLoading = false
          this.$bus.$emit(
            'message:push',
            `資料讀取失敗，請稍後再試
            ${err}`,
            'danger'
          )
        })
    },
    // 計算商品總件數及總價
    calTotal () {
      let sumTotal = 0
      let sumQuantity = 0
      this.carts.forEach((item) => {
        sumTotal += item.product.price * item.quantity
        sumQuantity += item.quantity
      })
      if (this.coupon.enabled === true) {
        this.cartTotal = sumTotal * (this.coupon.percent / 100)
        console.log('更新後的總價' + this.cartTotal)
      } else {
        this.cartTotal = sumTotal
      }
      this.orignalTotal = sumTotal
      this.quantityTatol = sumQuantity
    },
    // 新增 coupon (實際是搜尋 coupon)
    addCoupon () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/coupon/search`
      // 輸入 coupon 之前必須先戳一下 api/{{ uuid }}/coupon/search API 確定該 coupon 是存在的
      this.$http
        .post(url, { code: this.coupon_code })
        .then((response) => {
          // 若 coupon 存在就回寫回去到 this.coupon
          // 該資料會是一個物件格式
          this.coupon = response.data.data
          console.log(this.coupon.title)
          console.log(this.coupon.enabled)
          this.calTotal() // 更新價格
          this.$bus.$emit('message:push', '優惠碼已套用', 'success')
          this.isLoading = false
        })
        .catch((error) => {
          const errorData = error.response.data.errors
          if (errorData) {
            errorData.code.forEach((err) => {
              this.$bus.$emit(
                'message:push',
                `優惠碼套用失敗
              ${err}`,
                'danger'
              )
            })
            this.isLoading = false
          } else {
            const { message } = error.response.data
            this.$bus.$emit(
              'message:push',
              `優惠碼套用失敗
              ${message}`,
              'danger'
            )
            this.isLoading = false
          }
        })
    },
    createOrder () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/orders`
      const order = { ...this.form }
      // 如果有優惠卷就加入，請注意該 coupon 必須先執行過 this.addCoupon()
      // 主要會使用 enabled 屬性判斷該 coupon 是否可以使用
      // 如果沒有執行 this.addCoupon() 那麼 enabled 就會是 undefined，因此還是不會執行
      if (this.coupon.enabled) {
        order.coupon = this.coupon.code
      }
      this.axios
        .post(url, order)
        .then((res) => {
          if (res.data.data.id) {
            this.$bus.$emit('update-Total')
            this.$bus.$emit('message:push', '訂單建立成功', 'success')
            this.$router.push(`/confirm/${res.data.data.id}`)
            console.log(res.data.data.id)
          }
          this.$bus.$emit('update-Total')
          this.isLoading = false
        })
        .catch((err) => {
          const errorData = err.response.data.errors
          console.log(errorData)
          errorData.forEach((err) => {
            this.$bus.$emit('message:push',
              `建立訂單發生錯誤
            ${err}`,
              'danger')
          })
          this.isLoading = false
        })
    }
  }
}
</script>
